<template>
    

<div class="createTender_page">
    
    <header-top title='' back></header-top>
    <div class="fixed-top-div"></div>

    <div class="content fbcg">

        <div class="form-box">

            <form action="" class="form-container layui-form">
                <div class="form-group icon-gt">
                    <label for=""></label>
                    <input type="text" lay-verify="pId" @click=" showSelectProductTypeBox = true " v-bind:value="typeProductItem.p_name" readonly placeholder=""/>
                </div>

                <div class="form-group">
                    <label for=""></label>
                    <input type="text" v-model:value="order.sTitle" placeholder=""/>
                </div>

                <div class="form-group radio-group">
                    <label for=""></label>
                    <p style="display: inline-block;">
                        <input id="type1" 
                        v-bind:checked=" order.sType=='' " 
                        @click="order.sType='' " type="radio" style="opacity:0!important;" name="type-order"/>
                        <label for="type1" class="type-icon"></label>
                    </p>

                    <p style="display:inline-block;">
                        <input id="type2"

                         @click="order.sType='' " 
                         v-bind:checked=" order.sType=='' "
                         type="radio" style="opacity:0!important;" name="type-order"/>
                        <label for="type2" class="type-icon"></label>
                    </p>
                </div>

                <!-- ++++++++++++++++++++++++++++++++++++++++++++++ -->



                <div class="form-group">
                    <label for=""></label>
                    <input type="text" v-model:value="order.sQuality"  placeholder=""/>
                    <span class="unit" @click="alertSelectBox(3)"  v-text="unit.u_name"></span>
                </div>



                <div class="form-group">
                    <label for=""></label>
                    <input type="number" v-model:value="order.sPrice" placeholder=""/>
                    <span class="unit"  @click="alertSelectBox(2)" v-text="currency.c_name"></span>
                </div>


                <div class="form-group icon-gt">
                    <label for=""></label>
                    <input type="text" readonly @click="alertSelectBox(1)" v-bind:value="pack.p_type" placeholder=""/>
                </div>




<!-- 
                <div class="form-group">
                    <label for=""></label>
                    <input type="text" lay-verify="dw_id"  v-bind:value="unit.u_name"  readonly placeholder=""/>
                </div>

                <div class="form-group icon-gt">
                    <label for=""></label>
                    <input type="text" lay-verify="currencyId" readonly @click="alertSelectBox(2)"  v-bind:value="currency.c_name" placeholder=""/>
                </div> -->


                <div class="form-group icon-gt">
                    <label for=""></label>
                    <input type="text" lay-verify="tradeId" readonly @click="alertSelectBox(4)"  v-bind:value="tradeType.t_type" placeholder=""/>
                </div>

                <h3 class="h3-tips" @click="queryrealUserInfo"></h3>


                <!-- ++++++++++++++++++++++++++++++++++++++++ -->
                <div class="form-group icon-gt">
                    <label for=""></label>
                    <input type="text" @click="showSelectCityBox = true " v-model:value="typeAreaItem.dz_name" readonly placeholder=""/>
                </div>



                <div class="form-group">
                    <label for=""></label>
                    <input type="text" v-model:value="order.s_userName" placeholder=""/>
                </div>
                <div class="form-group">
                    <label for=""></label>
                    <input type="text" v-model:value="order.sPhone" placeholder=""/>
                </div>
                <div class="form-group">
                    <label for=""></label>
                    <input type="text" v-model:value="order.sAddress" placeholder=""/>
                </div>
                <div class="form-group">
                    <label for=""></label>
                    <input type="text"  v-model:value="order.sCompany" placeholder=""/>
                </div>
                <div class="form-group">
                    <label for=""></label>
                    <input type="text" v-model:value="order.sEmail" placeholder=""/>
                </div>

                <div class="form-group icon-gt">
                    <label for=""></label>
                    <input type="text" readonly @click="alertSelectBox(5)"  v-model:value="order.sDate" placeholder=""/>
                </div>

<!--                 <div class="form-group icon-gt">
                    <label for=""></label>
                    <input type="text" lay-verify="tPicktime" readonly @click="alertSelectBox(6)"  v-model:value="order.t_picktime" placeholder=""/>
                </div> -->

                <div class="form-group desc-group">
                    <label for=""></label>
                    <textarea v-model:value="order.sDetail" placeholder=""></textarea>
                </div>
                <div class="form-group image-group main-img" id="upload1">
                    <label for="" @click="test()">
                    (<span class='font-style-red'>{{ uploadImgCount }}</span>/5)</label>
                    <!--  -->
                    <drop-zone id="myVueDropzone" ref='uploadImg' :useFontAwesome="false" enctype="multipart/form-data" methods='post' 
                      :autoProcessQueue=false
                      :maxFileSizeInMB=10
                      :showRemoveLink=true

                      :maxNumberOfFiles="pFileCount"

                      :language="{dictRemoveFile:'',dictMaxFilesExceeded:'',dictCancelUpload:'',dictFileTooBig:'{{filesize}}Mib{{maxFilesize}}Mib'}"

                      acceptedFileTypes="image/*" 
                      url="http://192.168.0.123:8080/beta6.3/loadfile" 
                      v-bind:preview-template="template"
                      v-on:vdropzone-success="showSuccess"
                      v-on:vdropzone-removed-file="removeImg"
                      v-on:vdropzone-file-added="addImg"
                      v-on:vdropzone-mounted="initDropzone"
                      v-on:vdropzone-queue-complete="complete"
                    >


                    <div v-show=" uploadSwitch " class="img-upload-box dz-default dz-message">
                        <img src="../../../assets/imgs/publish/img-upload.png" alt="" />
                    </div>

                    <div class="dz-preview dz-image-preview" v-for=" (item,index) in pFileArr ">
                        <div class="dz-thumb">
                          <div class="dz-image">
                            <img :src="item"/>
                          </div>
                        </div>
                        <a class="dz-remove" href="javascript:undefined;" @click="deleteImg(index,'pri')" data-dz-remove=""></a>
                    </div>


                    </drop-zone>


                </div>
                <div id="upload2" class="form-group image-group info-img">
                    <label>(<span class='font-style-red'>{{ uploadImg2Count }}</span>/100)</label>

                    <!--  -->
                    <drop-zone id="myVueDropzone2" ref='uploadImg2' :useFontAwesome="true" enctype="multipart/form-data" methods='post' 
                      :autoProcessQueue=false
                      :maxFileSizeInMB=10
                      :showRemoveLink=true
                      :maxNumberOfFiles="pDetailCount"
                      :language="{dictRemoveFile:'',dictMaxFilesExceeded:'',dictCancelUpload:'',dictFileTooBig:'{{filesize}}Mib{{maxFilesize}}Mib'}"

                      acceptedFileTypes="image/*" 
                      url="http://192.168.0.123:8080/beta6.3/loadfile" 
                      v-bind:preview-template="template"
                      v-on:vdropzone-success="showSuccess2"
                      v-on:vdropzone-removed-file="removeImg2"
                      v-on:vdropzone-file-added="addImg2"
                      v-on:vdropzone-queue-complete="complete"
                    >

                    <div v-show=" uploadSwitch2 " class="img-upload-box dz-default dz-message">
                        <img src="../../../assets/imgs/publish/img-upload.png" alt="" />
                    </div>



                    <div class="dz-preview dz-image-preview" v-for=" (item,index) in pDetailArr ">
                        <div class="dz-thumb">
                          <div class="dz-image">
                            <img :src="item"/>
                          </div>
                        </div>
                        <a class="dz-remove" href="javascript:undefined;" @click="deleteImg(index,'det')" data-dz-remove=""></a>
                    </div>




                    </drop-zone>

                </div>
                <div class="form-group btn-group">
                    <input type="submit" value="" @click.prevent="submitPhoto" class="btn-control"/>
                </div>
                <div class="form-group agree-group">
                    <input type="checkbox" @click=" agree = !agree " id="agree"/>
                    <label for="agree" id="agreeStyle"></label>
                    <span><i></i></span>
                </div>
            </form>
        </div>


        <!---->

        <!---->

        <!---->

    </div>

  <!--  -->
  <transition>
    <product-type v-show="showSelectProductTypeBox" @selectProductType="selectProductType"></product-type>
  </transition>


  <!--  -->
  <transition>
    <city v-show='showSelectCityBox' @selectCity='selectCity'></city>
  </transition>



<!--  -->

  <div class="layui-m-layer" index="1" style="z-index:9999" @click=" closeMask " v-if=" showSelectBox ">
    <div class="layui-m-layershade"></div>
  </div>


  <div class="selectBox" id='selectBox' v-if=" showSelectBox ">

    <div class="selectBox_cell" v-show=" showSelectBoxId == 1 ">
      <h3></h3>
      <ul>
        <li v-for="(item,index) in unitList.Packs" @click="pack=item" v-text="item.p_type"></li>
      </ul>
    </div>

    <div class="selectBox_cell" v-show=" showSelectBoxId == 2 ">
      <h3></h3>
      <ul>
        <li v-for="(item,index) in unitList.Currencys" @click="currency = item" v-text="item.c_name"></li>
      </ul>
    </div>
    <div class="selectBox_cell" v-show=" showSelectBoxId == 3 ">
      <h3></h3>
      <ul>
        <li v-for="(item,index) in unitList.Units" @click="unit=item" v-text="item.u_name"></li>
      </ul>
    </div>


    <div class="selectBox_cell" v-show=" showSelectBoxId == 4 ">
      <h3></h3>
      <ul>
        <li v-for="(item,index) in unitList.Trades" @click="tradeType=item" v-text="item.t_type"></li>
      </ul>
    </div>

    <div class="selectBox_cell" v-show=" showSelectBoxId == 5 ">
      <h3>()</h3>
      <ul>
        <li v-for="item in 30" @click="endTime = item" v-text="item"></li>
      </ul>
    </div>

    <div class="selectBox_cell" v-show=" showSelectBoxId == 6 ">
      <h3></h3>
      <ul>

        <li v-for="item in 30" @click="getTime = item" v-text="item"></li>
        <li @click="getTime = 60">60</li>
        <li @click="getTime = 90">90</li>
        <li @click="getTime = 120">120</li>
        <li @click="getTime = 150">150</li>
        <li @click="getTime = 180">180</li>
        
      </ul>
    </div>

  </div>


</div>


</template>


<script>
    
import headerTop from '@/components/header/header'
import productType from '@/components/common/productType'
import city from '@/components/common/city'

import dropZone from 'vue2-dropzone'

import unit from '@/data/unit'


export default{

    components:{
        headerTop,
        productType,
        city,
        dropZone
    },

  data(){

    return{

      showSelectBoxId:3,
      showSelectBox:false,

      unitList:null,//

      uploadSwitch:true,//
      uploadImgCount:0,//

      showSelectProductTypeBox:false,//
      showSelectCityBox:false,//

      uploadSwitch2:true,//

      uploadImg2Count:0,//

      pFileCount:5,//
      pDetailCount:100,//


      order:{

        pId:"",
        pFile:"",
        pDetail:'',
        unitId:'',
        currencyId:'',
        usrId:this.$store.getters.getVip,
        packId:"",
        tradeId:"",
        areaId:"",
        sTitle:"",
        sType:"",
        sQuality:"",
        sPrice:"",
        s_userName:"",
        sPhone:"",
        sAddress:"",
        sCompany:"",
        sEmail:"",
        sDate:"",
        sDetail:"",

        listRar:"",
        pRar:"",
        pCargolist:""

      },

        unitList:[],//
        unit:{
            "u_name":""
        },//
        currency:{
            "c_name":""
        },//
        pack:{
            "p_type":""
        },//
        openPrice:true,//
        typeProductItem:{},//
        //
        tradeType:{
          "t_type":""
        },
        typeAreaItem:{},//

        pFileArr:[],//
        pDetailArr:[],//

        pFileArrStr:'',//str
        pDetailArrStr:'',//str


        newPriList:[],
        newDetList:[],

        //
        endTime:null,
        agree:false,//



    }

  },


  mounted(){


    //
    this.unitList = unit;

    //
    this.$nextTick(()=>{

      // console.log(this.$route.query);

      this.querySupplyInfo();



    })

  },
  watch:{


    endTime:function(val){
        this.order.sDate = val;
        this.showSelectBox = false;
    },

    //
    currency:function(val){
        this.order.currencyId = val.c_id;
        this.showSelectBox = false;

    },
    //
    pack:function(val){
        this.order.packId = val.p_id;
        this.showSelectBox = false;

    },
    //
    unit:function(val){

        this.order.dw_id = val.u_id;
        this.order.dw_name = val.u_name;

        this.order.unitId = val.u_id;

        this.showSelectBox = false;

    },
    //
    tradeType:function(val){
        this.order.tradeId = val.t_id;
        this.order.jy_name = val.t_type;
        this.showSelectBox = false;

    },




  },

  methods:{

    //
    querySupplyInfo(){

        // console.log(this.$route.query);
        // return;
        this.$http.post('/querySupplyByid1',this.$route.query).then((res)=>{


            if(res.data.code == 0){

                let order = res.data.response;

                console.log(order);
                //
                this.typeProductItem.p_name = order.lb_name;
                // this.typeProductItem.p_name = ;
                //
                let pFileStr = order.p_file.split(',');
                this.pFileArr = pFileStr.slice(0,pFileStr.length-1);

                //
                for(var i = 0;i<this.pFileArr.length;i++){
                    if(this.pFileArr[i]){
                        this.newPriList.push(this.pFileArr[i].substring(this.pFileArr[i].lastIndexOf('/')+1,this.pFileArr[i].length ))
                    }
                }

                //
                console.log(this.newPriList);

                //
                let pDetailStr = order.p_detail.split(',');
                this.pDetailArr = pDetailStr.slice(0,pDetailStr.length-1);

                //
                for(var i = 0;i<this.pDetailArr.length;i++){
                    if(this.pDetailArr[i]){
                        this.newDetList.push( this.pDetailArr[i].substring(this.pDetailArr[i].lastIndexOf('/')+1,this.pDetailArr[i].length ))
                    }

                }
                //
                console.log(this.newDetList);

                //
                this.pack.p_type = order.pack_name;
                //
                this.unit.u_name = order.dw_name;
                // 
                this.tradeType.t_type = order.jy_name;
                //
                this.currency.c_name = order.bz_name;
                //
                this.typeAreaItem.dz_name = order.dz_name;
                //
                this.uploadImgCount = this.pFileArr.length;
                this.pFileCount = 5 - this.pFileArr.length;
                //
                this.uploadImg2Count = this.pDetailArr.length;
                this.pDetailCount = 100 - this.pFileArr.length;



                /*+++++++++++++++++++++++++++++++++*/



                this.order.sTitle = order.s_title;//
                this.order.sType = order.s_type;//
                this.order.sQuality = order.s_quality;//
                this.order.dw_name = order.dw_name;//
                this.order.sPrice = order.s_price;//
                this.order.bz_name = order.bz_name;//
                this.order.packId = order.pack_id;//ID



                this.typeAreaItem.a_name = order.dz_name;//
                this.order.s_userName = order.s_userName;//
                this.order.unitId = order.unit_id;//ID
                this.order.tradeId = order.trade_id;//ID
                this.order.packid = order.pack_id;//ID
                this.pack.p_type = order.pack_name;//
                this.tradeType.t_type = order.jy_name;//
                this.order.sid = order.s_id;//ID
                this.order.areaId = order.area_id;//
                this.order.pId = order.p_id;//
                this.order.currencyId = order.currency_id;//ID


                this.order.sPhone = order.s_phone;//
                this.order.sAddress = order.s_address;//
                this.order.sCompany = order.s_company;//
                this.order.sEmail = order.s_email;//
                this.order.sDate = order.s_date;//

                this.order.sDetail = order.s_detail;//









            }

        })
    },

    //()
    queryrealUserInfo(){

        this.$http.post('/querySmrzInfo',{vip:this.$store.getters.getVip}).then((res)=>{
            //
            if(res.data.code==0){
                let info = res.data.response;
                this.order.s_userName = info.name;
                this.order.sPhone = info.phone;
                this.order.sAddress = info.address;
                this.order.sEmail = info.email;
                this.order.sCompany = info.company;
                this.typeAreaItem.dz_name = info.areaName;

                if(info.a_id3!=""){
                    this.typeAreaItem.a_id = info.a_id3;
                    this.order.areaId = info.a_id3;
                }else{
                    this.typeAreaItem.a_id = info.a_id2;
                    this.order.areaId = info.a_id2;
                }

            }
        }).catch(function(res){
            console.log(res);
        })
    },



    //
    deleteImg(ind,from){
        let e = event||window.event;
        let target = e.target||e.srcElement;
        //
        // target.parentNode.parentNode.removeChild(target.parentNode);


        //
        if(from=="pri"){

            //
            this.pFileArr.splice(ind,1);
            //()
            this.newPriList.splice(ind,1);



            //
            this.pFileCount = 5 - this.pFileArr.length;
            //
            this.uploadImgCount = this.$refs.uploadImg.dropzone.files.length + this.pFileArr.length;
            //
            this.uploadSwitch = this.uploadImgCount>=5?false:true;


        }else{

            //
            this.pDetailArr.splice(ind,1);
            //
            this.newDetList.splice(ind,1);


            this.pFileCount = 100 - this.pDetailArr.length;
            this.uploadImg2Count =  this.$refs.uploadImg2.dropzone.files.length + this.pDetailArr.length;
            this.uploadSwitch2 = this.uploadImg2Count>=100?false:true;



        }

    },








    //
    alertSelectBox(id){
      console.log(id);
      this.showSelectBoxId = id;
      this.showSelectBox = true;
    },
    //
    closeMask(){
      this.showSelectBox = false;
    },
    test(){
      //
      // console.log(this.$refs.uploadImg.getAcceptedFiles())
      //
      console.log(this.$refs.uploadImg)

      this.uploadSwitch = false;

    },
    //dropzone
    initDropzone(){

      console.log(this.$refs.uploadImg)

    },
    //
    addImg(file){


      if(this.$refs.uploadImg.dropzone.files.length >= this.pFileCount){
        this.uploadSwitch = false;
      }

      if(this.$refs.uploadImg.dropzone.files.length > this.pFileCount){
        this.$refs.uploadImg.removeFile(file);
      }



      this.uploadImgCount = this.$refs.uploadImg.dropzone.files.length + this.pFileArr.length;
    },
    //
    removeImg(file){
      if(this.$refs.uploadImg.dropzone.files.length >= this.pFileCount){
        this.uploadSwitch = false;
      }else{
        this.uploadSwitch = true;
      }
      this.uploadImgCount = this.$refs.uploadImg.dropzone.files.length + this.pFileArr.length;
    },
    //
    showSuccess(file,res){
      //
      if(res.code==0){
        console.log(res)
        // this.pFileArrStr += res.response;
        let imgSrc = res.response;

        this.newPriList.push(imgSrc.substring(0,imgSrc.length-1));


      }

    },

    //
    addImg2(file){
      if(this.$refs.uploadImg2.dropzone.files.length >= 100){
        this.uploadSwitch2 = false;
      }
      if(this.$refs.uploadImg2.dropzone.files.length > 100){
        this.$refs.uploadImg2.removeFile(file);
      }


      this.uploadImg2Count = this.$refs.uploadImg2.dropzone.files.length + this.pDetailArr.length ;
    },
    //
    removeImg2(file){
      if(this.$refs.uploadImg2.dropzone.files.length >= 100){
        this.uploadSwitch2 = false;
      }else{
        this.uploadSwitch2 = true;
      }

      this.uploadImg2Count = this.$refs.uploadImg2.dropzone.files.length + this.pDetailArr.length ;
    },
    //
    showSuccess2(file,res){
      //
      if(res.code==0){
        console.log(res)
        let imgSrc = res.response;
        this.newDetList.push(imgSrc.substring(0,imgSrc.length-1));


      }
    },

    //
    complete(){

      //
      // this.priList = [];
      // this.detList = [];

      //0
      if( this.$refs.uploadImg.getUploadingFiles().length == 0 && this.$refs.uploadImg2.getUploadingFiles().length ==0 ){
        // //
        // let imgDesc = document.getElementsByClassName("priList");
        // //1
        // let newUploadImg1Count = this.$refs.uploadImg.getAcceptedFiles().length;
        // //2
        // let newUploadImg2Count = this.$refs.uploadImg2.getAcceptedFiles().length;
        // for(let i = 0;i < this.uploadImgCount  ;i++){
        //   //
        //   this.priList.push(imgDesc[i].value);
        // }
        // for(let i = this.uploadImgCount ; i < imgDesc.length;i++){
        //   this.detList.push(imgDesc[i].value)
        // }
        

        //
        this.submitForm();
      }

    },




    //
    template() {
      return `
              <div class="dz-preview dz-file-preview">
                <div class="dz-thumb">
                  <div class="dz-image">
                      <img data-dz-thumbnail />
                  </div>
                  
                  <div class="dz-progress">
                    <span class="dz-upload" data-dz-uploadprogress></span>
                  </div>

                  <div class="dz-error-message"><span class="error-info" data-dz-errormessage></span></div>

                  <div class="dz-details">
                    <div class="dz-size"><span data-dz-size></span></div>
                  </div>
                </div>
              </div>


          `;
    },
    //
    submitPhoto(){


      //
      layer.open({
        type:2,
        shadeClose:false,
      })


      //
      if(this.$refs.uploadImg.dropzone.files.length <= 0 && this.$refs.uploadImg2.dropzone.files.length <= 0){
        this.submitForm();
      }else{
        //
        this.$refs.uploadImg.processQueue();
        this.$refs.uploadImg2.processQueue();
      } 





    },
    //
    submitForm(){


      //
      this.order.pFile = this.newPriList.toString();
      this.order.pDetail = this.newDetList.toString();


      console.log(this.order);


      // return ;
      //
      this.$http.post('/ModifySupply',this.order).then((res)=>{
        //
        layer.closeAll();
        if(res.data.code==0){
          layer.msg("");
        }else{
          layer.msg(res.data.message);
        }
      })


    },
    //
    selectProductType(item){
      this.typeProductItem.p_name = item.textContent;
      this.order.pId = item.getAttribute("data-id");

      this.showSelectProductTypeBox = false;
    },
    selectCity(item){
      // console.log(item);

      this.order.area_id = item.getAttribute("data-id");
      this.typeAreaItem.dz_name = item.textContent;

      this.showSelectCityBox = false;
    }

  }


}

</script>

<style lang='less'>
@import '../../../assets/css/variable'; 




.selectBox{

  z-index:99999;
  position: fixed;
  width:40%;
  top:0;
  left:0;
  right:0;
  bottom:0;
  margin:auto;
  text-align:center;
  display:flex;
  align-items:center;

  h3{
    font-size:.7rem;
    padding:.5rem 0;
  }
  .selectBox_cell{
    width:100%;
    background: #fff;
    padding:0 10%;
    max-height:70%;
    overflow: scroll;
  }

  li{

    line-height: 1.8rem;
    font-size:.6rem;
    color:#444444;
    border-bottom:1px solid #37C6BD;
  }

  li:last-child{
    border:none;
  }

}




.createTender_page{
    .fixed();
    z-index:999;
}

.fbcg{

  .form-box{

    .form-group{

      font-size:.7rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;


      .unit{



        &:after{


          content:'';

          position: absolute;
          top:.75rem;

          bottom:0;
          margin:auto;

          border-style:solid;
          border-width:.5rem;
          border-color: #959595 transparent transparent transparent;
        }

      }

      position: relative;
      border-bottom:1px solid #D5D5D5;
      padding:0 5/@root-font-size;

    }
    .radio-group{
      line-height:30/@root-font-size;
      input[type=radio]:checked + label{
        background: #93DB91;
      }

      .type-icon{
        vertical-align: middle;
        display: inline-block;
        width:15/@root-font-size;
        height:15/@root-font-size;
        border:2px solid #C1C2BD;
        border-radius:50%;
        margin-right:.3rem;
        padding:0;
        position: relative;

      }


    }

    .image-group{

      padding:10px;
      position: relative;
      background: #F0F0F0;
      border:none;
      label{
        text-align: left;
        padding-bottom:5px;
        font-size:.7rem;
      }


      input[type=file]{
        opacity: 0;
        width:100%;
        height:100%;
        top:0;
        left:0;
        position: absolute;
        display: none;
      }

      .img-upload-box{
        /*margin-bottom:10px;*/
        height:80/@root-font-size;
        margin:.2rem;
        position: relative;
        /*padding:10px 0;*/
        overflow: hidden;
        display: flex;
        justify-content: center;



        img{
          height:100%;
          vertical-align: middle;
        }


        p.img{
          width:40%;
          padding-right: 5/@root-font-size;
          overflow: hidden;

          img{
            max-width:100%;
          }
        }
        p.desc{
          width:60%;
          textarea{
            border:1px solid #f00;
            border-radius:5px;
            padding:4/@root-font-size;
          }
        }



      }

      .flex-box{
        display: flex;
        flex-wrap: wrap;

        justify-content: space-between;
        height:auto;
      }



    }

    .agree-group{
      background: #F0F0F0;
      border:none;
      text-align: center;
      line-height:22/@root-font-size;
      padding-bottom:30/@root-font-size;

      display: flex;
      align-items: center;
      justify-content: center;

      color:#0042FF;

      label{
        width:17/@root-font-size;
        height:17/@root-font-size;
        border:2px solid #999;
        background: #fff;
        padding:0;
        margin:0 5px;
        position: relative;

      }



      input[type=checkbox]:checked + label{
        border-color:#f00;

        &:after{
            content:' ';
            display: block;
            width:.5rem;
            height:.5rem;
            position: absolute;
            background: #f00;
            top:0;
            bottom:0;
            left:0;
            right:0;
            margin:auto;
        }
      }


/*
      input[type=checkbox]:checked +label i{
        display: block;
      }
*/
    }

    .btn-group{
      background: #F0F0F0;
      border:none;

      .btn-control{
        margin:20/@root-font-size 0;
        background: #389E3A;
        line-height:2rem;
        border-radius:8px;
        font-size:.7rem;
        width:100%;
        color:#fff;
        text-align: center;

      }
    }


    .desc-group{
      label{
        display: block;
        padding:5px 0;
      }

    }

    textarea{
      width:100%;
      height:80/@root-font-size;
      resize: none;
      font-size:.7rem;

    }
    label{
      display: inline-block;
      width:6em;
      padding-right:5/@root-font-size;
      text-align:left;
      margin:.3rem 0;
    }

    input[type=text],input[type=number]{
      line-height:12/@root-font-size;
      padding:10/@root-font-size 0 10/@root-font-size 5/@root-font-size;
      width:42%;
      font-size:.7rem;

    }


    .h3-tips{
      line-height:40/@root-font-size;
      background: #EAEAEA;
      padding:0 5/@root-font-size;
      font-size:.75rem;


    }




  }

}




/*.dz-preview{
  border:2px solid #f00;
}*/



#upload1,#upload2{


  .dropzone{

    padding:.1rem;
    min-height: inherit;

    .dz-message{
      margin:0;
    }

    .dz-image{
      width:100%;
      /*height:auto;*/
      display: flex;
      justify-content: center;
    }

    .dz-desc{
      position: relative;
      z-index:99;
      margin-left:2%;

      textarea{
        border:1px solid #ddd;
        padding:.2rem;
        height:5rem;

      }
    }

    .dz-preview{

      display:block;
      /*min-height:4rem;*/
      display:flex;
      min-height:3rem;
      max-height:5rem;


      div.dz-desc,div.dz-thumb{
        flex:1;
      }

      div.dz-thumb{
        width:50%;
        overflow: hidden;
      }
      div.dz-desc{
        width:48%;
      }

      &:hover{
        z-index:90;
      };

  
    }
    .dz-details{
/*      line-height: inherit;
      height:200px;*/
      font-size:.8rem;
      text-align:center;
      width:100%;

      padding:inherit;
      min-width: inherit;
      min-height: inherit;

      .dz-size{
        font-size:.6rem;
      }


    }

    .dz-remove{
      left:50%;
      bottom:0;
      line-height:.5rem;
      font-size:.6rem;
      width:3rem;
      margin-left:-1.5rem;
      background: #66CBE5;
      display: block;
      opacity: 1;
      visibility: visible;

    }

    .dz-error-message{

      font-size:.5rem;
      width:auto;
      /*bottom:0;*/
      opacity: 1;
      top:1rem;
      z-index:995;

    }

    .dz-progress{
      z-index:99;
      border-radius:1rem;
      height:.6rem;
      width:4rem;
      margin-left:-2rem;
      top:0;
      left:50%;

      background: #FDF6E3;

      opacity: 1;
      visibility: visible;

      .dz-upload{
        background:#2BA38E;
      }
      
    }


  }

  
}




</style>


